export default {
	data() {
		return {
			sum: 8000,
			radio:" 1"
		}
	},
	methods: {
		onChange(event) {
			this.setData({
				radio: event.detail
			});
		},
		onClick(event) {
			const {
				name
			} = event.currentTarget.dataset;
			this.setData({
				radio: name
			});
		}
	}
}
